* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
  padding: 0;
  margin: 0;
}

html, body {
  height: 100%;
  width: 100%;
  color: white;
  text-shadow: rgba(22, 71, 140, 0.2) 1px 0 4px;
}

body {
  background: #3a7bd5;
  background: -webkit-gradient(linear, left top, left bottom, from(#00b8e0), to(#3a7bd5));
  background: -webkit-linear-gradient(top, #00b8e0, #3a7bd5);
  background: -o-linear-gradient(top, #00b8e0, #3a7bd5);
  background: linear-gradient(to bottom, #00b8e0, #3a7bd5);
  background-attachment: fixed;
  text-align: center;
}

h1 {
  margin: auto;
  margin-top: 25px;
  margin-bottom: 30px;
  font-weight: 700;
  font-family: 'Lobster Two', cursive;
  -webkit-transition: all .1s ease-out;
  -o-transition: all .1s ease-out;
  transition: all .1s ease-out;
}

input[type=text] {
  outline: none;
  padding: 0.5rem;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, .4);
  font-weight: normal;
}
::-moz-placeholder { /* Firefox 19+ */
  color: rgba(0, 0, 0, .4);
  font-weight: normal;
}
:-ms-input-placeholder { /* IE 10+ */
  color: rgba(0, 0, 0, .4);
  font-weight: normal;
}
:-moz-placeholder { /* Firefox 18- */
  color: rgba(0, 0, 0, .4);
  font-weight: normal;
}

.container {
  padding: 1.5rem;
}

input.search {
  width: 100%;
}

.searchResults {
  padding: 1rem 0;
}

.station {
  background-color: white;
  padding: 0.8rem;
  color: black;
  font-size: 1rem;
  text-align: left;
  text-shadow: none;
}

.station .location {
  color: rgba(0, 0, 0, .6);
  font-size: 0.9rem;
}

.flash {
  position: absolute;
  top: 80px;
  width: 100%;
  text-align: center;
  opacity: 0;
  -webkit-transition: opacity .1s ease-in;
  -o-transition: opacity .1s ease-in;
  transition: opacity .1s ease-in;

  &.visible {
    opacity: 1;
    z-index: 999;
  }

  > span {
    background: rgba(78, 214, 245, 0.36);
    padding: 10px 20px;
    border-radius: 30px;
  }
}



